<template>
  <div>
    <div style="padding: 10px 10px; border-radius: 10px; background-color: #222222;">
      <el-input type="textarea" :rows="1" autosize v-model="keyword" @input="onKeywordChange" placeholder="0x" clearable></el-input>
    </div>
    <div class="rank-list">
      <rank-intro v-for="(item, index) in list" :key="index" :rank="item"></rank-intro>
    </div>
  </div>
</template>
<script>
import RankIntro from './rank_intro'
import pool from "@/utils/pool"
import common from "@/utils/common"
import events from "@/utils/events"
import base from "@/utils/base"
export default {
  mixins: [base],
  name: 'AccountRank',
  props: {
    accountType: {
      type: String,
      default: 'receiver'
    }
  },
  components: {
    RankIntro
  },
  data() {
    return {
      loading: false,
      allList: [],
      list: [],
      keyword: '',
    }
  },
  created() {
    this.eventHandlers[events.tokenChanged] = () => {
      this.getRankList()
    }
    this.eventHandlers[events.txConfirmed] = param => {
      if (param.type === common.getTxTypes().confirmReceived) {
        this.getRankList()
      }
    }

  },
  methods: {
    baseInit() {
      this.getRankList()
    },
    formatAccountAddress(row) {
      if (this.screenWidth > 652) {
        return row.account.toLowerCase()
      } else {
        return common.formatLongText(row.account.toLowerCase())
      }
    },
    formatArguingBalance(row) {
      return common.weiToUnit(row.arguingBalance) + ' ' + this.tokenSymbol
    },
    formatFailedBalance(row) {
      return common.weiToUnit(row.failedBalance) + ' ' + this.tokenSymbol
    },
    formatSuccessBalance(row) {
      return common.weiToUnit(row.successBalance) + ' ' + this.tokenSymbol
    },
    formatArguingCnt(row) {
      return row.arguingCnt.toNumber()
    },
    formatFailedCnt(row) {
      return row.failedCnt.toNumber()
    },
    formatSuccessCnt(row) {
      return row.successCnt.toNumber()
    },
    async onKeywordChange(val) {
      if (val === '') {
        this.list = this.allList
        return
      }
      let tmpList = []
      this.allList.forEach(item => {
        if (item.account.toLowerCase().indexOf(this.keyword.toLowerCase()) !== -1) {
          tmpList.push(item)
        }
      })

      if (tmpList.length === 0) {
        const keywordAddress = common.getAddress(val)
        if (null !== keywordAddress) {
          let r = null
          if (this.accountType === this.roles.sender) {
            r = await pool.payerInfos(keywordAddress)
          } else {
            r = await pool.payeeInfos(keywordAddress)
          }
          tmpList.push({
            account: r.account,
            title: common.hexToString(r.title),
            content: common.hexToString(r.content),
            successCnt: r.sucCnt,
            successBalance: r.sucBal,
            failedCnt: r.failCnt,
            failedBalance: r.failBal,
            arguingCnt: r.argCnt,
            arguingBalance: r.argBal,
          })
        }
      }
      this.list = tmpList
    },
    async getRankList() {
      if (null === this.poolItem) {
        return
      }
      this.loading = true
      await this.getTopList()
      this.loading = false
    },
    async getTopList() {
      let userList = []
      if (this.accountType === this.roles.receiver) {
        userList = await pool.getPayeeTop()
      } else if (this.accountType === this.roles.sender) {
        userList = await pool.getPayerTop()
      }

      let tmpList = []
      for (let i = 0; i < userList.length; i++) {
        const r = userList[i]
        tmpList.push({
          account: r.account,
          title: common.hexToString(r.title),
          content: common.hexToString(r.content),
          successCnt: r.sucCnt,
          successBalance: r.sucBal,
          failedCnt: r.failCnt,
          failedBalance: r.failBal,
          arguingCnt: r.argCnt,
          arguingBalance: r.argBal,
        })
      }

      tmpList = tmpList.sort(this.rankSort())
      tmpList.forEach((item, index) => {
        item.index = index + 1
      })
      this.allList = tmpList

      this.onKeywordChange(this.keyword)
    },
    rankSort() {
      return (a1, a2) => {
        let r = -1
        if (a2.successBalance.gt(a1.successBalance)) {
          r = 1
        }
        return r
      }
    }
  }
}
</script>
<style>
.rank-list {
  overflow-y:auto;
  overflow-x:hidden;
  margin-top: 10px;
  padding: 10px 10px;
  border-radius: 10px;
  background-color: #111;
}
</style>
